<template>
  <el-row class="warp">
    <el-col :span="24" class="warp-breadcrum">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item :to="{ path: '/' }"><b></b></el-breadcrumb-item>
        <el-breadcrumb-item></el-breadcrumb-item>
      </el-breadcrumb>
    </el-col>

    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <div class="stay_out">0</div>
      <el-form :inline="true" :model="filters" style="margin-top:10px;">
        <el-form-item label="ID">
          <el-input v-model="filters.nickname" @keyup.enter.native="handleSearch"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="filters.upgrade" placeholder="">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="handleSearch"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!-- -->
    <el-table :data="record" highlight-current-row style="width:100%;" id="out-table">
      <el-table-column type="index" width="60"></el-table-column>
      <el-table-column prop="name" label="ID" width="160"></el-table-column>
      <el-table-column prop="desc" label="" width="120"></el-table-column>
      <el-table-column prop="productPic" label="" width="100">
        <template slot-scope="scope">
          <img :src="scope.row.productPic" alt="" style="width: 50px;height: 50px;">
        </template>
      </el-table-column>
      <el-table-column prop="type" label="" width="120"></el-table-column>
      <el-table-column prop="taskName" label="" width="120" sortable></el-table-column>
      <el-table-column prop="taskName" label="" width="120" sortable></el-table-column>
      <el-table-column prop="taskName" label="" width="160" sortable></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button size="small" @click="adjustmentDialog(scope.$index,scope.row)"></el-button>
          <el-button size="small" type='text'></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="passVisible" :close-on-click-modal="false" class="tip">
      <div class="content">
        <div class="title"></div>
        <div class="matter"></div>
        <el-button size="small" class="anniu cancel"></el-button>
        <el-button size="small" type="primary" class="anniu"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="rejectVisible" :close-on-click-modal="false" class="tip">
      <div class="content">
        <div class="result"></div>
        <el-input type="textarea" class="textarea" style="height:100px;"></el-input>
        <el-button size="small" class="anniu cancel"></el-button>
        <el-button size="small" type="primary" class="anniu"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="checkVisible" :close-on-click-modal="false" class="tip">
      <div class="content">
        <div class="result"></div>
        <el-input type="textarea" class="textarea" style="height:100px;"></el-input>
        <el-button size="small" type="primary" class="through"></el-button>
      </div>
    </el-dialog>
  </el-row>
</template>

<script>
  import axios from 'axios'
  export default {
    name: "DestoonFinanceCard",
    data(){
      return{
        options:[
          {value:''},
          {value:''},
          {value:''},
        ],
        filters:{},
        record:[],
        passVisible:false,
        rejectVisible:false,
        checkVisible:false
      }
    },
    methods:{
      handleSearch(){

      }
    }
  }
</script>

<style lang="scss">
  .toolbar{
    margin-top:30px;
    float:left;
  }
  .stay_out{
    width:200px;
    height:80px;
    background-color: #ccc;
    text-align: center;
    line-height:80px;
    font-size:30px;
    float:left;
    margin:-10px 30px 0 0;
    cursor: pointer;
  }
  .content{
    width:300px;
    height:200px;
    border:1px solid #000;
    border-radius: 5px;
    .title{
      text-align: center;
      height:50px;
      line-height: 50px;
      border-bottom:1px solid #000;
      font-weight:bold;
      font-size:20px;
    }
    .matter{
      height:100px;
      line-height: 100px;
      border-bottom:1px solid #000;
      text-align: center;
      font-size:18px;
    }
    .anniu{
      margin-top:8px;
    }
    .cancel{
      margin-left:90px;
    }
    .result{
      height:50px;
      line-height:50px;
      font-size:18px;
      margin-left:20px;
    }
    .through{
      margin:10px 0 0 120px;
    }
  }
  .tip{
    .el-dialog{
      width:25%;
    }
  }
  .textarea{
    margin:0 20px;
    width:260px;
    height:100px;
    .el-textarea__inner {
      height: 100px;
    }
  }

</style>



